import L from 'leaflet';
import { jsonp } from '../util';
export default {
  "class": L.Class.extend({
    initialize: function initialize(key) {
      this.key = key;
    },
    geocode: function geocode(query, cb, context) {
      jsonp('https://dev.virtualearth.net/REST/v1/Locations', {
        query: query,
        key: this.key
      }, function (data) {
        var results = [];

        if (data.resourceSets.length > 0) {
          for (var i = data.resourceSets[0].resources.length - 1; i >= 0; i--) {
            var resource = data.resourceSets[0].resources[i],
                bbox = resource.bbox;
            results[i] = {
              name: resource.name,
              bbox: L.latLngBounds([bbox[0], bbox[1]], [bbox[2], bbox[3]]),
              center: L.latLng(resource.point.coordinates)
            };
          }
        }

        cb.call(context, results);
      }, this, 'jsonp');
    },
    reverse: function reverse(location, scale, cb, context) {
      jsonp('//dev.virtualearth.net/REST/v1/Locations/' + location.lat + ',' + location.lng, {
        key: this.key
      }, function (data) {
        var results = [];

        for (var i = data.resourceSets[0].resources.length - 1; i >= 0; i--) {
          var resource = data.resourceSets[0].resources[i],
              bbox = resource.bbox;
          results[i] = {
            name: resource.name,
            bbox: L.latLngBounds([bbox[0], bbox[1]], [bbox[2], bbox[3]]),
            center: L.latLng(resource.point.coordinates)
          };
        }

        cb.call(context, results);
      }, this, 'jsonp');
    }
  }),
  factory: function factory(key) {
    return new L.Control.Geocoder.Bing(key);
  }
};