import L from 'leaflet';
import { getJSON } from '../util';
export default {
  "class": L.Class.extend({
    options: {
      serviceUrl: 'https://api.what3words.com/v2/'
    },
    initialize: function initialize(accessToken) {
      this._accessToken = accessToken;
    },
    geocode: function geocode(query, cb, context) {
      //get three words and make a dot based string
      getJSON(this.options.serviceUrl + 'forward', {
        key: this._accessToken,
        addr: query.split(/\s+/).join('.')
      }, function (data) {
        var results = [],
            latLng,
            latLngBounds;

        if (data.hasOwnProperty('geometry')) {
          latLng = L.latLng(data.geometry['lat'], data.geometry['lng']);
          latLngBounds = L.latLngBounds(latLng, latLng);
          results[0] = {
            name: data.words,
            bbox: latLngBounds,
            center: latLng
          };
        }

        cb.call(context, results);
      });
    },
    suggest: function suggest(query, cb, context) {
      return this.geocode(query, cb, context);
    },
    reverse: function reverse(location, scale, cb, context) {
      getJSON(this.options.serviceUrl + 'reverse', {
        key: this._accessToken,
        coords: [location.lat, location.lng].join(',')
      }, function (data) {
        var results = [],
            latLng,
            latLngBounds;

        if (data.status.status === 200) {
          latLng = L.latLng(data.geometry['lat'], data.geometry['lng']);
          latLngBounds = L.latLngBounds(latLng, latLng);
          results[0] = {
            name: data.words,
            bbox: latLngBounds,
            center: latLng
          };
        }

        cb.call(context, results);
      });
    }
  }),
  factory: function factory(accessToken) {
    return new L.Control.Geocoder.What3Words(accessToken);
  }
};